.App {
  text-align: center;
  padding: 20px;
}

h1 {
  color: #1a73e8; 
}

input {
  padding: 10px;
  font-size: 16px;
  width: 70%;
  border-radius: 30px;
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

button {
  padding: 10px 20px;
  font-size: 16px;
  background-color: #1a73e8; 
  color: white;
  cursor: pointer;
  border: none;
  border-radius: 20px;
}

button:hover {
  background-color: #0a58ca; 
}

.result-answer {
  background-color: lightgrey;
  padding: 10px;
  margin-top: 20px;
  margin-bottom: 20px;
  margin-left: 10%;
  width: 30%;
  border-radius: 15px;
}

.question-item {
  background-color: #1a73e8;
  color: white;
  padding: 10px;
  margin-top: 20px;
  margin-bottom: 20px;
  margin-left: auto;
  margin-right: 10%;
  width: 30%;
  border-radius: 15px;
}


.news-item {
  background-color: lightgrey;
  padding: 10px;
  margin-bottom: 20px;
  margin-left: 10%;
  width: 30%;
  border-radius: 15px;
}

.proofreading-item {
  /* background-color: lightgrey; */
  padding: 1px;
  margin-bottom: 20px;
  margin-left: 5%;
  width: 20%;
  border-radius: 3px;
}

.result h3 {
  color: #1a0dab;
}

.result p {
  color: #5f6368; 
}

.no_dot {
  list-style-type: none;
}